import React from "react";
import { Works } from "./Works.data";
import { Title } from "../../../globalStyle";
import {
  ContentSection,
  FeaturedSection,
  ImgSection,
  WorksContainer
} from "./Works.styles";

const FeaturedProjects = () => {
  return (
    <FeaturedSection className="center" id="featured">
      <Title>Featured Projects</Title>
      {Works.map((data) => {
        return (
          <WorksContainer>
            <ImgSection
              href={data.link}
              target="_blank"
              rel="noopener noreferrer"
            >
              <img
                alt={data.comment}
                src={process.env.PUBLIC_URL + `/images/works/${data.id}.jpg`}
              />
            </ImgSection>
            <ContentSection>
              <a href={data.link} target="_blank" rel="noopener noreferrer">
                <h1>{data.header}</h1>
              </a>
              <p>{data.comment}</p>
            </ContentSection>
          </WorksContainer>
        );
      })}
    </FeaturedSection>
  );
};

export default FeaturedProjects;
